.dcolumns {
  display: inline-block;
  padding-right: 5px;
  padding-left: 5px;
  vertical-align: top;
  box-sizing: border-box;
  width: 50%;
}

.color-code-option {
  height: 13px;
  width: 13px;
  border-radius: 13px;
  margin: auto;
  display: inline-block;
  vertical-align: middle;
  padding-left: 10px;
  line-height: 22px;
}

.date-tz-picker {
  display: flex;
  .date-picker {
      width: 125px;
  }
  .time-picker {
      width: 90px;
  }
  .tz-picker {
      width: 90px;
  }
}
.custom-file {
  .custom-file-label {
      border: 1px solid #4C9E00;
      height:24px;
      &.is-invalid {
          border: 1px solid red;
      }
      &.invalid-feedback {
          display: block;
          width: 100%;
          margin-top: -0.75rem;
          color: #EA485B;
          font-size: 11px;
          font-weight: bold;
      }
      &::after {
          content: "Browse";
          background-color: #4C9E00;
          color: white;
          height:23px;line-height: 8px;
      }
  }
}
.uploadList{
  ul{margin:0;padding:0px;list-style: none;
      li{font-size:12px;padding:2px 2px 2px 20px;display: block;overflow: auto;position:relative;
          a{display: block;position: absolute;left:2px;top:2px;padding:0px 3px;}
          span{display:block; overflow: hidden;text-overflow: ellipsis;white-space: nowrap;cursor: pointer;}
      }
  }
}

.ng-select {

  &.form-control {
      padding: 0;
      height: 100%;
  }
  &::ng-deep .ng-select-container  {      
      min-height: 22px;
      min-width: 100px;
      height: 22px;
      border-radius: 3px;
      // font-size: 12px;
      border: none;
  }

  &::ng-deep .ng-select-container .ng-input {
      top: 0 !important;
  }

  &::ng-deep .ng-dropdown-panel .ng-dropdown-panel-items .ng-option {
      height: 22px;
      // font-size: 12px;
      padding: 2px 12px;
  }

  &.ng-select-multiple::ng-deep .ng-select-container {
      height: 100%;
  }
  &.ng-select-disabled>::ng-deep .ng-select-container {
      background-color: #e9ecef !important;
  }
}

.input-label {
  color: #4D4D51;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 0.5px;
}

.dmo-tooltip {
  color: #4C9E00 !important;
