.form-check {
  .form-check-input {
      margin-top: 0.4rem;
  }

  .form-check-label {
      color: #4D4D51;
      font-size: 12px;
      font-weight: bold;
      letter-spacing: 0.46px
  }
}

.form-control {
  height: 24px;
  border-radius: 3px;
  font-size: 12px;
}

.input-group {
  align-items: center;
  flex-wrap: nowrap;

  .input-group-prepend {
      .btn {
          height: 24px;
          display: flex;
          outline: none;
          box-shadow: none;
      }
  }

  &.is-invalid {
      .input-group-prepend {
          button {
              border: 1px solid red;
          }
      }

      input.form-control {
          border: 1px solid red;
      }

      & ~ .invalid-feedback {
          display: block;
      }
  }
  
}

.invalid-feedback {
  font-size: 11px;
  font-weight: bold;
}

