dp-date-picker {
  display: block;
  font-size: 12px;
  width: 100%;
  padding: 0;
  border: 0;

  &::ng-deep {
      .dp-picker-input {
          height: 24px !important;
          font-size: 12px !important;
          width: 100% !important;
          color: #495057 !important;
          border: 1px solid #ced4da;
          border-radius: 3px;
          padding: 6px 12px !important;

          &[disabled] {
              background-color: #e9ecef;
          }
      }

      dp-day-calendar {
          .dp-calendar-nav-container {
              background-color: #f8f9fa!important;

              .dp-calendar-nav-left, .dp-calendar-nav-right {
                  color: #4c9e00!important;
              }

              .dp-current-location-btn {
                  background: #4c9e00;
                  border-color: #4c9e00
              }
          }

          .dp-weekdays {
              font-size: 80% !important;

              .dp-calendar-weekday {
                  color: #17a2b8 !important;
                  font-weight: 500 !important;
                  width: 1.6rem;
                  height: 1.6rem;
                  line-height: 2rem;
                  font-style: italic;
              }
          }

          .dp-calendar-day {
              width: 1.6rem;
              height: 1.6rem;
              border-radius: .25rem;
          }
  
      }

      dp-month-calendar {
          .dp-calendar-month {
              width: 45px !important;
              height: 45px !important;
              border: none !important;
              border-radius: .25rem !important;
          }
      }

      .dp-selected {
          background: #4c9e00!important;
      }

      .dp-current-day {
          border-color: #4C9E00 !important;
      }
  }
}

